<template>
  <div id="page-config">
    <el-row>
      <el-col :span="12"><p class="config-button" @click="changeConfig('theme')">{{themeName}}</p></el-col>
      <el-col :span="12"><p class="config-button" @click="changeConfig('lang')">{{langName}}</p></el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'page-config',

  computed: {
    themeName() {
      return (this.theme === "dark" ? "Dark Theme": "Light Theme");
    },

    langName() {
      return (this.lang === "eng" ? "English": "FranÃ§ais");
    }
  },

  props: {
    theme: String,
    lang: String
  },

  methods: {
    changeConfig(configType) {
      console.log("Config changed!");
    }
  }
}
</script>

<style scoped>
#page-config {
  margin-top: 1rem;
  color: #ffffff;
}

.config-button {
  font-family: 'lato',sans-serif;
  font-weight: 400;
}

.config-button:hover {
  color: #fb966e;
  cursor: pointer;
}
</style>



