<template>
  <div id="blog-summary">
    <p class="title-text">
      {{title}}
    </p>
  </div>
</template>

<script>
export default {
  name: 'blog-summary',

  data() {
    return {
      title: "Solicite's Blog"
    }
  }
}
</script>

<style scoped>
.blog-summary {
  margin-top: 5rem;
}

.title-text {
  margin-top: 2rem;
  font-family: 'lato', sans-serif;
  font-weight: 900;
  font-size: 300%;
  color: #fb966e;
}
</style>



