<template>
  <div id="contact-element">
    <icon :name="config.icon" class="icon-element"></icon>
    <span class="title-text">
      {{config.name}}
    </span>
    <p class="normal-text">
      {{config.value}}
    </p>
  </div>
</template>

<script>
export default {
  name: 'contact-element',

  props: {
    config: {
      type: Object,
      required: true
    }
  }
}
</script>

<style scoped>
.title-text {
  margin-left: 2rem;
  margin-top: 2rem;
  font-family: 'lato', sans-serif;
  font-weight: 700;
  font-size: 200%;
  color: #ffffff;
}

.normal-text{
  margin-top: 2rem;
  font-family: 'lato', sans-serif;
  font-weight: 400;
  font-size: 150%;
  color: #ffffff;
}

.icon-element {
  margin-top: 1.5rem;
  width: 50px;
  height: auto;
  color: #ffffff;
}
</style>



