<template>
  <div id="portefolio">
    <p class="title-text">
      {{title}}
    </p>
  </div>
</template>

<script>
export default {
  name: 'portefolio',

  data() {
    return {
      title: "Portefolio"
    }
  }
}
</script>

<style scoped>
.portefolio {
  margin-top: 5rem;
}

.title-text {
  margin-top: 2rem;
  font-family: 'lato', sans-serif;
  font-weight: 900;
  font-size: 300%;
  color: #fb966e;
}
</style>



