<template>
  <div id="profile">
    <p class="title-text">
      {{description}}
    </p>

  </div>
</template>

<script>
export default {
  name: 'profile',

  data() {
    return {
      description: ", currently works for Gemalto SA as a Front-end designer and developper."
    }
  }
}
</script>

<style scoped>
#profile {
  margin-top: 15rem;
}

.title-text {
  margin-top: 2rem;
  font-family: 'lato', sans-serif;
  font-weight: 900;
  font-size: 300%;
  color: #fb966e;
}
</style>



