<template>
  <div id="app">
    <el-row>
      <el-col :span="6">
        <transition name="side-bar-transition" enter-active-class="animated fadeInDown" leave-active-class="animated bounceOutRight">
          <side-bar v-show="!isInIndex"></side-bar>
        </transition>
        <br />
      </el-col>
      <el-col :span="8" :offset="1">
        <transition name="main-content-transition" enter-active-class="animated fadeInDown">
        <router-view></router-view>
      </transition>
      </el-col>
      <el-col :span="4" :offset="5">
        <page-config :theme="theme" :lang="lang"></page-config>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import SideBar from './components/sidebar/SideBar.vue'
import PageConfig from './components/pageconfig/PageConfig.vue'

export default {
  name: 'app',

  data() {
    return {
      theme: 'dark',
      lang: 'eng'
    }
  },

  computed: {
    isInIndex: function() {
      return (this.$route.name === "index"?true: false);
    }
  },

  components: {
    'side-bar': SideBar,
    'page-config': PageConfig
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Ubuntu:300,400,700');
@import url('https://fonts.googleapis.com/css?family=Lato:400,700,900');

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  width: 100%;
  height: 100%;
}
</style>



