<template>
  <div id="index-page">
    <div>
      <router-link v-for="menuItem in menuItems" :to="menuItem.routing" class="no-underline">
        <p class="menu-text">
        {{menuItem.name}}
        </p>
      </router-link>
      <p class="divider">
        ____________
      </p>
      <div class="icon-table">
        <icon v-for="iconElement in linkIcons" :name="iconElement" class="icon-element"></icon>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'index-page',

  data() {
    return {
      menuItems: [{"name":"About Me","routing":"/profile"}, {"name":"My Blog","routing":"/blog"},{"name":"Portefolio","routing":"/portefolio"},{"name":"Contact Me","routing":"/contact"}],
      linkIcons: ["twitter","facebook","instagram","github"],
    }
  }
}
</script>

<style scoped>
.icon-table {
  margin-top: 3rem;
}

.icon-element {
  width: 50px;
  height: 50px;
  margin-right: 5rem;
  color: #ffffff;
}

.icon-element:hover {
  color: #fb966e;
  cursor: pointer;
}

#index-page {
  margin-top: 9rem;
}

.divider {
  margin-top: -2rem;
  text-align: left;
  font-family: 'lato', sans-serif;
  font-weight: 900;
  font-size: 200%;
  padding-left: 1rem;
  color: #fb966e;
}

router-link {
  border-bottom: 0px;
}

.menu-text {
  padding-left: 1rem;
  text-align: left;
  margin-top: -1rem;
  margin-bottom: 2.5rem;
  font-family: 'lato', sans-serif;
  font-weight: 900;
  font-size: 360%;
  color: #ffffff;
}

.menu-text:hover {
  border-left: 5px solid #fb966e;
  cursor: pointer;
}
</style>



