<template>
  <div id="side-bar">
    <img src="../../assets/icons/profile1.jpg" class="profile"/>
    <p class="title-text">
      {{name}}
    </p>
    <router-link v-for="(menuItem,index) in menuItems" :to="menuItem.routing" class="no-underline" @click.native="changeSelection(index)">
      <p class="menu-text" :class="{'selected': index === isSelected}">
        {{menuItem.name}}
      </p>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'side-bar',

  data() {
    return {
      isSelected: -1,
      name: "Yifan XIONG",
      menuItems: [{"name":"About Me","routing":"/profile"}, {"name":"My Blog","routing":"/blog"},{"name":"Portefolio","routing":"/portefolio"},{"name":"Contact Me","routing":"/contact"}]
    }
  },


  methods: {
    changeSelection(arg) {
      this.isSelected = arg;
    }
  }
}
</script>

<style scoped>
.profile {
  margin-top: 3rem;
  width: 10rem;
  height: auto;
  border-radius: 8px;
}

.selected {
  color: #fb966e !important;
}

#side-bar {
  margin-left: 3rem;
}

.title-text {
  margin-top: 2rem;
  font-family: 'lato', sans-serif;
  font-weight: 900;
  font-size: 300%;
  color: #ffffff;
}


.menu-text {
  padding-left: 0.5rem;
  text-align: left;
  margin-top: -1rem;
  margin-bottom: 2.5rem;
  font-family: 'lato', sans-serif;
  font-weight: 400;
  font-size: 150%;
  color: #ffffff;
}

.menu-text:hover {
  cursor: pointer;
  border-left: 3px solid #fb966e;
}
</style>



