<template>
  <div id="contact">
    <p class="title-text">
      {{title}}
    </p>
    <contact-element v-for="contactConfig in contactList" :config="contactConfig"></contact-element>
  </div>
</template>

<script>
import ContactElement from '../../elements/contact/ContactElement'

export default {
  name: 'contact',

  data() {
    return {
      title: "Contact Me",
      contactList: [{"name": "Mobile", "value": "+33 (0) 6 59 21 98 74","icon": "mobile"}, {"name": "Email", "value": "yifanxiong812@gmail.com","icon": "envelope"}, {"name": "Address", "value": "400 Avenue Roumanille, Biot, France","icon": "address-book"}]
    }
  },

  components: {
    'contact-element': ContactElement
  }
}
</script>

<style scoped>
.contact {
  margin-top: 5rem;
}

.title-text {
  margin-top: 2rem;
  font-family: 'lato', sans-serif;
  font-weight: 900;
  font-size: 300%;
  color: #fb966e;
}
</style>



