import Vue from 'vue'
import Router from 'vue-router'
import IndexPage from '@/components/indexpage/IndexPage'
import Profile from '@/components/profile/Profile'
import Contact from '@/components/contact/Contact'
import BlogSummary from '@/components/blog/BlogSummary'
import Portefolio from '@/components/portefolio/Portefolio'

Vue.use(Router)

export default new Router({
  mode: 'history',

  routes: [
    {
      path: '/',
      name: 'index',
      component: IndexPage
    },
    {
      path: '/profile',
      name: 'profile',
      component: Profile
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact
    },
    {
      path: '/blog',
      name: 'blog',
      component: BlogSummary
    },
    {
      path: '/portefolio',
      name: 'portefolio',
      component: Portefolio
    }
  ]
})



